import {
    GET_QUOTES,
    GetQuotesStateType,
    QuoteActionTypes,
} from '../types/QuoteTypes';

const initialStateGetQuotes: GetQuotesStateType = {
    quotes: {
        id: '',
        timestamp: new Date(),
        name: '',
        meta: {}
    },
};

export const getQuotesReducer = (
    state = initialStateGetQuotes,
    action: QuoteActionTypes,
): GetQuotesStateType => {
    switch (action.type) {
        case GET_QUOTES:
            return {
                ...state,
                quotes: action.payload,
            };
        default:
            return state;
    }
}